<template>
    <div class = 'from'>
        <form action = 'http://169.254.150.1:3000/uploadrout' method = 'POST' enctype = 'multipart/form-data'>
            <input type = 'text' ref = 'name' name = 'name' placeholder = '姓名'/>
            <input type = 'text' ref = 'grade' name = 'grade' placeholder = '班级名称'/>
            <input type = 'file' class = 'file' ref = 'files' @change = 'change' name = 'img'/>
            <p class = 'filep' @click = 'files'>选择上传文件</p>
            <p>{{fileName}}</p>
            <input type = 'submit' value = '提交' class = 'button' ref = 'sub'/>
            <p class = 'button buttons' @click= 'submit'>提交</p>
        </form>
        
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        data () {
            return {
                fileName: '',
                file: null
            }
        },
        methods: {
             files () {
                this.$refs.files.click()
            },
            change (e) {
                this.fileName = this.$refs.files.files[0].name;
                this.file = this.$refs.files.files[0];
            },
            submit () {
                let name = this.$refs.name.value;
                let grade = this.$refs.grade.value;
                if (name && grade) {
                   this.$refs.sub.click();
                }
            }
        }
    }
</script>

<style scoped>
    .from{
        width: 100%;
        height: auto;
        background:beige;
    }
    .from form{
        width:400px;
        height:auto;
        margin:0 auto;
        border:1px solid #ccc;
        padding:0 10px;
    }
    .from input{
        display:block;
        margin:0 auto;
        margin:10px 0;
        width:100%;
        height:40px;
        background:none;
        border:1px solid #ccc;
    }
    .from .file{
        display:none;
    }
    .from .filep{
        background:aquamarine;
        height:30px;
        text-align:center;
        line-height:30px;
        color:#fff;
    }
    .from .button{
        width: 100px;
        height:30px;
        border:none;
        background:rgb(0, 162, 255);
        text-align:center;
        line-height:30px;
        color:#fff;
        display:none;
        margin:0 auto;
        margin:10px auto;
    }
    .from .buttons {
        display: block;
    }
</style>



